import React from 'react';
import AppHeader from '../components/AppHeader';
import {Row,Col,Layout } from 'antd';

const { Header, Footer, Sider, Content } = Layout;

const Home = () => {
    return(
        <>
            <AppHeader/>
            <div>
            <Layout>
                <Header>Header</Header>
                <Content>Content</Content>
                <Footer>Footer</Footer>
            </Layout>
            </div>
        </>
    );
};

export default Home;